.head{
    height: 60px;
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #b64743;
}
.head__blue{
    border-bottom: 2px solid #448286;
} 
.head__navyBlue{
    border-bottom: 2px solid #3e6f92;
}


.head-title{
    color: white
}
ul{
    list-style: none;
    display: flex;
}
ul li{
    margin-left: 10px;
}
ul li button{

    position: relative;
}
ul li button img{
    width: 17px;
    height: 17px;
    text-align: center;
    position: absolute;
    top:6px;
    left:8px;
}
.btn{
    background-color: #dd6662;
    border: none;
    color: white;
    padding: 8px 12px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border-radius: 8px;
    padding-left: 32px;
}
.btn__blue{
    background-color: #5e9ca0;
}
.btn__navyBlue{
    background-color: #5889ac;
}


